html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "IBM Plex Sans Arabic", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  overflow-x: hidden;
}

p {
  font-size: 1.7rem;
}

.container {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "IBM Plex Sans Arabic", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
  font-weight: 700;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.titulos-span {
  font-weight: 300 italic;
  display: inline-block;
  position: relative;
  color: #2f2838;
  font-size: 2.7rem;
  padding-left: 5.3rem;
}

.titulos-span::before, .titulos-span::after {
  content: "";
  position: absolute;
  width: 120px;
  height: 1px;
  background-color: #2f2838;
  top: 0.8em;
}

.titulos-span::before {
  left: -7rem;
}

.e404 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.e404 .titulo-404 {
  color: #2f2838;
}

.boton-marron {
  background-color: #83624f;
  padding: 1.5rem 3rem 1.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.boton-marron:hover {
  background-color: #83624f;
  cursor: pointer;
}

.boton-verde {
  background-color: #eb4b2d;
  padding: 1.5rem 3rem 1.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.boton-verde:hover {
  background-color: #eb4b2d;
  cursor: pointer;
}

.boton-blanco {
  background-color: #eb4b2d;
  padding: 1.5rem 3rem 1.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
  padding-top: 1rem;
  padding-bottom: 0.1rem;
}
.boton-blanco:hover {
  background-color: #eb4b2d;
  cursor: pointer;
}

.boton-rojo-block {
  margin-bottom: 1rem;
  background-color: #bd0707;
  padding: 1.5rem 3rem 1.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.boton-rojo-block:hover {
  background-color: #bd0707;
  cursor: pointer;
}

.boton-blanco-verde {
  background-color: #FFFFFF;
  padding: 1.5rem 3rem 1.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #58ab37;
}
.boton-blanco-verde:hover {
  background-color: white;
  cursor: pointer;
}

.boton-blanco-marron {
  background-color: #FFFFFF;
  padding: 1.5rem 3rem 1.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #83624f;
}
.boton-blanco-marron:hover {
  background-color: white;
  cursor: pointer;
}

.soft-headline {
  margin-top: 2rem;
  display: inline-block;
  border-top: 3px solid #83624f;
  min-height: 0.1rem;
  width: 100%;
  max-width: 70%;
}

.ml-0 {
  margin-left: -5.2rem;
}

.contenido-centrado {
  max-width: 80rem;
}

td .imagen-tabla {
  width: 10rem;
  align-self: center;
  justify-self: center;
}

.mensaje-exito {
  background-color: #eb4b2d;
  padding: 1rem 1.2rem;
  color: #FFFFFF;
  width: fit-content;
  font-weight: 700;
}

.alert {
  margin-bottom: 3rem;
}

.display-none {
  display: none;
}

.no-webp .inicio {
  background-image: url(../img/hero3.jpg);
}

.webp .inicio {
  background-image: url(../img/hero3.webp);
}

.header {
  height: 10vh;
}
.header.inicio {
  background-repeat: no-repeat;
  background-position: center right;
  width: 100%;
  height: 100%;
  position: relative;
  min-height: max-content;
  margin-right: 8rem;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  position: absolute;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  opacity: 0.9;
  height: 100%;
  width: 100%;
}
.contenido-header h1 {
  text-align: left;
  color: #2f2838;
  font-weight: 700;
  line-height: 1.5;
  letter-spacing: 1.5;
  max-width: 60rem;
  margin-top: 0rem;
}
.contenido-header img {
  height: 60px;
  width: auto;
}

.nav .contenido-header {
  opacity: 1;
  flex-direction: row;
  position: initial;
  width: 150%;
  display: inline;
  min-height: 5rem;
}
.nav .contenido-header .barra {
  display: flex;
  justify-content: space-between;
}
.nav .contenido-header .barra .navegacion a:last-of-type {
  margin-right: 0rem;
}

@media (min-width: 1024px) {
  .nav img {
    margin-left: 2rem;
  }

  .header {
    padding: 1rem 0 3rem 0;
    height: 8vh;
    max-width: 90%;
  }

  .inicio {
    background-position: center right;
    background-size: 50%;
    background-color: transparent;
  }
  .inicio .redes-sociales {
    display: flex;
    list-style: none;
  }
  .inicio .contenido-header {
    justify-content: space-between !important;
    align-items: initial;
    justify-content: space-around;
    background-color: transparent;
    min-height: 50rem;
    position: relative;
    opacity: 1;
  }
}
.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #2f2838;
  text-decoration: none;
  font-size: 2.2rem;
  font-weight: 700;
  display: block;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 3rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 10rem;
  }
}
.navegacion a:hover {
  color: #eb4b2d;
}

.nosotros {
  display: flex;
  flex-direction: column;
  height: max-content;
  margin-top: 5rem;
  margin-bottom: 5rem;
}
.nosotros .img-nosotros {
  width: 100%;
}
.nosotros .nosotros-content {
  background: #2f2838;
  margin-top: 0px;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.nosotros .nosotros-content .container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.nosotros .nosotros-content h2 {
  color: #FFFFFF;
  font-weight: 700;
  text-align: end;
  padding: 1rem 3rem 0 0;
}
.nosotros .nosotros-content span {
  color: #eb4b2d;
  padding-right: 2rem;
}
.nosotros .nosotros-content span::after, .nosotros .nosotros-content span::before {
  background-color: #FFFFFF;
}
.nosotros .nosotros-content h2 {
  font-size: 4rem;
}
.nosotros .nosotros-content p {
  color: #FFFFFF;
  text-align: justify;
  padding: 0 3rem;
}
.nosotros .nosotros-content a {
  margin: 3rem;
  text-align: center;
}

@media (min-width: 768px) {
  .nosotros {
    height: max-content;
  }

  .img-nosotros {
    width: 60%;
  }
}
@media (min-width: 1024px) {
  .nosotros {
    flex-direction: row;
    height: 70rem;
    max-height: 70rem;
  }
  .nosotros .nosotros-content {
    margin-top: 5rem;
  }
  .nosotros .img-nosotros {
    max-width: 90rem;
  }
}
.productos {
  display: flex;
  flex-direction: column;
}
.productos span {
  color: #eb4b2d;
}
.productos span::after, .productos span::before {
  background-color: #2f2838;
}
.productos h2 {
  text-align: start;
  color: #2f2838;
  margin-left: 1rem;
}
.productos .lista-productos {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
}
.productos .lista-productos .producto {
  display: flex;
  flex-direction: column;
  box-shadow: 0.1rem 0.5rem 1.2rem rgba(47, 40, 56, 0.1);
  margin-bottom: 2rem;
}
.productos .lista-productos .producto .content-producto {
  display: flex;
  height: 5rem;
  justify-content: space-between;
  align-content: center;
}
.productos .lista-productos .producto .content-producto:nth-child(1) {
  justify-content: flex-start;
}
.productos .lista-productos .producto a {
  display: flex;
  justify-content: center;
  align-items: center;
}
.productos .lista-productos .producto a img {
  height: 100%;
  filter: invert(100%);
}
.productos .lista-productos .producto h3 {
  margin-top: 1rem;
  margin-left: 1rem;
  font-size: 1.8rem;
  letter-spacing: 1.3;
  text-align: center;
  font-weight: 700;
  color: #2f2838;
}
@media (min-width: 768px) {
  .productos .lista-productos {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .productos .lista-productos {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .productos h2 {
    margin-left: 5rem !important;
  }
}

.producto-page {
  margin-top: 10rem;
}

.item-list {
  color: #FFFFFF;
}

.testimoniales {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 10rem;
  min-height: max-content;
  margin-bottom: 5rem;
  background-color: #fafafa;
}
.testimoniales span {
  color: #eb4b2d;
  padding-left: 0;
}
.testimoniales span::after {
  display: none;
}
.testimoniales span::before {
  left: -0.5rem;
  width: 160px;
  top: 2em;
  background-color: #eb4b2d;
}
.testimoniales h2 {
  color: #2f2838;
}
.testimoniales .testimoniales-content {
  display: flex;
  row-gap: 5rem;
  column-gap: 10rem;
  flex-direction: column;
}
.testimoniales .testimoniales-content .testimonial {
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 0.1rem 0.5rem 1.2rem rgba(47, 40, 56, 0.1);
  margin-bottom: 3rem;
}
.testimoniales .testimoniales-content img {
  display: flex;
  height: 160px;
  width: 200px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.testimoniales .testimoniales-content p, .testimoniales .testimoniales-content h3 {
  text-align: justify;
  padding: 0 2rem;
  color: #2f2838;
}
.testimoniales .testimoniales-content h3 {
  align-self: flex-start;
  color: #2f2838;
  font-weight: 700;
}

@media (min-width: 768px) {
  .testimoniales-content {
    flex-direction: row !important;
    gap: 5rem;
  }
}
.webp .contacto {
  background-image: url(../img/contacto.webp);
}

.contacto {
  height: max-content;
  background-repeat: no-repeat;
  background-color: #2f2838;
  background-size: cover;
  margin-bottom: 0;
  margin-top: -5rem;
  display: flex;
}
.contacto span {
  margin-top: 5rem;
  color: #FFFFFF;
}
.contacto span::after, .contacto span::before {
  background-color: #FFFFFF;
}
.contacto .formulario {
  padding: 3rem;
  height: auto;
  width: auto;
}
.contacto .formulario label {
  color: #FFFFFF;
  font-weight: 700;
  margin-top: 3rem;
}
.contacto .formulario input:not([type=submit]), .contacto .formulario textarea {
  margin: 0.6rem 0 0.6rem 0;
  width: 100%;
  display: block;
  background-color: transparent;
  border: 1px solid #FFFFFF;
  outline: none;
  color: #FFFFFF;
}
.contacto .formulario textarea {
  height: 10rem;
}

@media (min-width: 768px) {
  .contacto {
    background-color: transparent;
    justify-content: flex-start;
    opacity: 1;
  }
  .contacto .formulario input:not([type=submit]), .contacto .formulario textarea, .contacto .formulario label {
    width: 150%;
  }
}
.footer {
  min-height: 8vh;
  max-height: 8vh;
  background-color: #FFFFFF;
}
.footer .content-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer .content-footer p {
  color: #2f2838;
  font-weight: 300 italic;
  margin-bottom: 0;
}

.info-table {
  margin: 4rem 0;
  width: 100%;
  border-spacing: 0;
}
.info-table thead {
  background-color: #eb4b2d;
}
.info-table thead th {
  color: #FFFFFF;
  padding: 2rem;
}
.info-table td {
  text-align: center;
}
.info-table tbody tr {
  background-color: #2f2838;
  color: #FFFFFF;
}
.info-table tbody tr:nth-child(odd) {
  background-color: #483d56;
}

.titulo-crud {
  color: #2f2838;
}

.administrador {
  display: flex;
  margin-top: 5rem;
}
.administrador .barra-nav {
  background: #FFFFFF;
  height: 100vh;
  margin-top: 4rem;
}
.administrador .active {
  background-color: #fcfcfc;
}
.administrador .crud {
  flex-grow: 2;
}
.administrador h3 {
  color: #2f2838;
  margin: 0;
  padding: 2rem;
  padding-left: 0;
  font-size: 2rem;
  margin-top: 0;
}
.administrador .option {
  display: flex;
}
.administrador .option svg {
  justify-self: center;
  align-self: center;
  height: 3rem;
}

.form {
  width: 100%;
  background: #FFFFFF;
  margin-top: 5rem;
}
.form label, .form input, .form textarea {
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  padding: 0.5rem;
  border-radius: 1rem;
}

@media (min-width: 768px) {
  .form {
    width: 50%;
  }
}
.crud {
  margin-top: 4rem;
}
.crud .titulo-form {
  padding-top: 3rem;
  color: #2f2838;
}
.crud a {
  text-align: center;
}